.circle {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    border: 2px solid black;
    display: inline-block;
    margin: 5px;
    cursor: pointer;
}
.circle.red {
    background-color: red;
}
.circle.green {
    background-color: green;
}
.circle.white {
    background-color: white;
}
.diamond-layout {
    position: relative;
    width: 100px;
    height: 100px;
    margin: 20px auto;
    background-color: green; /* Field background */
    transform: rotate(45deg); /* Rotate the diamond */
}

.base {
    position: absolute;
    width: 27px;
    height: 27px;
    background-color: white;
    border: 2px solid black;
    transform: rotate(-45deg); /* Rotate bases back to square */
    cursor: pointer;
}

.base.occupied {
    background-color: blue; /* Occupied base color */
}

/* Positioning bases */
.base:nth-child(1) { /* Home base */
    top: 0%;
    right: 0;
    transform:  rotate(0deg);
}

.base:nth-child(2) { /* First base */
    top: 0;
    left: 0;
    transform:  rotate(0deg);
}

.base:nth-child(3) { /* Second base */
    bottom: 0;
    left: 0;
    transform:  rotate(0deg);
}

.base:nth-child(4) { /* Third base */
    bottom: 0;
    right: 0;
    transform:rotate(0deg);
}

.scoreboard {
    text-align: center;
}

.outs-strikes-balls {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px; /* Add spacing between sections */
    margin-top: 20px;
}

.outs, .strikes, .balls {
    text-align: center;
}

.runner-actions {
    position: absolute;
    top: 50%; /* Align vertically with first base */
    left: 75%; /* Position to the right of first base */
    transform: translate(-50%, -50%); /* Center the buttons */
    display: flex;
    flex-direction: column; /* Stack buttons vertically */
    gap: 10px; /* Add spacing between buttons */
}

.runner-actions button {
    padding: 5px 10px;
    font-size: 14px;
    cursor: pointer;
}